/**
 * 
 * https://github.com/tsuyoshiwada/sweet-scroll
 */
import SweetScroll from 'sweet-scroll'

export default function commonBackToTop() {
  // ID
  const baseName = '#common-BackToTop'
  // CSS
  const headerElement = '[data-scroll-header]'

  const backToTop = new SweetScroll({ // eslint-disable-line
    trigger: baseName, // CSS
    header: headerElement, // CSS
    duration: 1000, // 
    easing: 'easeOutQuint', // 
    offset: 0, // 
    vertical: true, // 
    horizontal: false, // 
    cancellable: true, // 
    updateURL: false, // URL
    preventDefault: true, // 
    stopPropagation: true, // 
    after() {
      const destination = document.getElementsByTagName('body')[0]
      destination.setAttribute('tabindex', '-1')
      destination.focus()
    },
  })
}
