import SweetScroll from 'sweet-scroll'

/**
 * 
 * https://github.com/tsuyoshiwada/sweet-scroll
 * @example
 * import { scroller } from '@utility/scroller'
 * const element = document.getElementById('element')
 * scroller.toElement(element)
 * element.setAttribute('tabindex', '-1')
 * element.focus()
 */
const scroller = new SweetScroll({
  trigger:
    'a[href*="#"]:not([href*="/"]):not([href="#"]):not(#v1-back-to-top):not(sup a):not(.-noSmoothScroll)', // CSS
  header: document.getElementById('st-LocalNav') || document.getElementById('st-GlobalHeader'), // CSS
  duration: 1000, // 
  easing: 'easeOutQuint', // 
  offset: 0, // 
  vertical: true, // 
  horizontal: false, // 
  cancellable: true, // 
  updateURL: true, // URL
  preventDefault: true, // 
  stopPropagation: true, // 
  before(offset, $trigger, scrollElement) {
    if (scrollElement.ctx.hash) {
      const destination = document.getElementById(scrollElement.ctx.hash.replace('#', ''))
      destination.setAttribute('tabindex', '-1')
      destination.focus()
    }
  },
})

export { scroller }
